				<p>Krav Maga (u prevodu ''Kontakt Borba'') je sistem samoodbrane nastao 30 tih godina prolog vijeka na osnovu iskustva Imi Lichtenfelda koji je u to vrijeme bio bokser i hrva. U vrijeme nacistikog progona jevreja u drugom svijetskom ratu Imi Lichtenfeld osmislio je prve taktiko operativne radnje samoodbrane realne i primjenjive u ulinom nasilju koje kasnije prilikom svoje imigracije u Izrael razvija u vojnim snagama. Ubrzo nakon toga Krav Maga postaje slubena borilaka vjetina Izraelske vojske a nakon 1964 godine prilagoava se policijskim snagama a kasnije i civilima kao jedan realan i efikasan sistem samoodbrane. Krav Maga se sve do 1996 godine mogla se uiti i trenirati u Izraelu u slubama bezbjednosti i civilnim klubovima a od 1996 godine poinje irenje Krav Maga u svijetu poev od Sjedinjenih Amerikih Drava a ubrzo nakon toga u zemlje Evropske Unije i irom svijeta. Krav Maga takodje biva prihvaena u mnogim jedinicam policije i vojske u svijetu pa ak i u dravama naeg okruenja. U Bosnu i Hercegovinu Krav Maga stie 2006 godine odravanjem prvog seminara i kursa u Sarajevu od strane Internacionalnog Krav Maga Instituta ''IKMI'' iz Evropske Unije, otkad poinje sa radom sekcija sa redovnim treninzima u sportskom klubu Sfera i na kraju formiranjem ''Krav Maga kluba samoodbrane'' u Sarajevu kao prvog kluba Krav Maga u Bosni i Hercegovini. Krav Maga je sistem samoodbrane koji obuhvata vie borilakih vjetina (Karate, Kung-fu, Boks, Jiu-Jitsu). Navedeni sistem samoodbrane se temelji na prirodnim reakcijama ovjeka i njegovim refleksinim pokretima i shodno tome sve tehnike koje Krav Maga obuava su realne i situacionog su karaktera. Kako je sistem nastao u slubama bezbjednosti same tehnike su prilagoene uglavnom za upotrebu u vojsci i policiji, medjutim kako se Krav Maga irila na civile razvila je verzije obuke za civile, zatitare, sistem tehnika za zatitu treih i VIP osoba. </p>
